import back from '@/components/back'

export default {
    components:{
        back
    },
    data(){
        return {
            ageStr: '',
            text: '',
            item: [],
            info: [],
            show: false,
            show2: false,
            list: [[]],
            date: '',
            pat: null,
            user: this.$store.state.userInfo,
        }
    },
    created(){
        this.pull()
    },
    methods: {
        pull(){
            this.$get('PlatFormAPI/ServicePackItem/GetServicePackItemBySPRId',this.$pick({
                ServicePackRecordId: this.$route.query.id
            })).then(o => {
                this.info = o
                this.info.forEach(v => {
                    this.list[0].push(v.ServiceItemName)
                })
            })
            this.$get('PlatFormAPI/ServicePackItem/GetServicePackRecordDetail',this.$pick({
                ServicePackRecordId: this.$route.query.id
            })).then(o => {
                this.pat = o
            })
        },
        confirm(){
            if(!this.item.length){
                return this.$toast('请选择服务执行项目')
            }
            if(!this.date){
                return this.$toast('请选择执行日期和时间')
            }
            let obj = null
            this.info.forEach(v => {
                if(v.ServiceItemName == this.item[0]){
                    obj = v
                }
            })
            this.$post('PlatFormAPI/FamilyDoctorTeam/ServiceUseRegister',this.$pick({
                PatientId: this.pat.PatientInfo.PatientId,
                ServicePackRecordId: obj.ServicePackRecordId,
                ServicePackId: obj.ServicePackId,
                ServiceItemId: obj.ServiceItemId,
                ServiceRecordDetailId: obj.ServiceRecordDetailId,
                ExecuteDateTime: this.date,
                Situation: this.text,
                DrId: this.user.Data.User.DoctorId,
                DrName: this.user.Data.User.DoctorName,
            })).then(o => {
                this.$toast('提交成功')
                this.$router.back()
            })
        }
    }
}



